form{
    margin-top: 7vh;
    margin-bottom: 10vh;
    /* border: solid black; */
    padding: 0%;
    display: flex;
    min-height: 6vh;
    justify-content: center;
    cursor: pointer;
    margin-left:5%;
    margin-right: 5%;
}
input{
    min-width: 55%;
    padding-left: 4px;
    font-size: 15px;
    background: #f7fffe;
    border: none;
    border: 2px solid #333;
}
/* add button style */
.addButton{
     min-width: 20%;
     font-size: 15px;
     border: none;
     background: #f7fffe;
}

a{
display: block;
/* width: 200px; */
/* height: 40px; */
line-height: 40px;
/* font-size: 18px; */
font-family: sans-serif;
text-decoration: none;
color: #333;
border: 2px solid #333;
letter-spacing: 2px;
text-align: center;
position: relative;
transition: all .35s;
}

a span{
position: relative;
z-index: 2;
}

a:after{
position: absolute;
content: "";
top: 0;
left: 0;
width: 0;
height: 100%;
background: #ff003b;
transition: all .35s;
}

a:hover{
color: #fff;
}

a:hover:after{
width: 100%;
}

.filter{
  min-width: 5%;
  font-size: 15px;
  background: #f7fffe;
  border: 2px solid #333;
}
/*cool title style */
.title{
  text-align: center;
  margin-top: 7vh;
  font-size: 27px;
}
h1 {
  position: relative;
  font-family: 'Montserrat', Arial, sans-serif;
  font-weight: 600;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-shadow: 0 0 0.15em #1da9cc;
  user-select: none;
  white-space: nowrap;
  filter: blur(0.007em);
  animation: shake 3.5s linear forwards;
}

h1 span {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  -webkit-clip-path: polygon(10% 0%, 44% 0%, 70% 100%, 55% 100%);
          clip-path: polygon(10% 0%, 44% 0%, 70% 100%, 55% 100%);
}

h1::before,
h1::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
}

h1::before {
  animation: crack1 2.5s linear forwards;
  -webkit-clip-path: polygon(0% 0%, 10% 0%, 55% 100%, 0% 100%);
          clip-path: polygon(0% 0%, 10% 0%, 55% 100%, 0% 100%);
}

h1::after {
  animation: crack2 2.5s linear forwards;
  -webkit-clip-path: polygon(44% 0%, 100% 0%, 100% 100%, 70% 100%);
          clip-path: polygon(44% 0%, 100% 0%, 100% 100%, 70% 100%);
}

@keyframes shake {
  5%, 15%, 25%, 35%, 55%, 65%, 75%, 95% {
      filter: blur(0.018em);
      transform: translateY(0.018em) rotate(0deg);
  }

  10%, 30%, 40%, 50%, 70%, 80%, 90% {
      filter: blur(0.01em);
      transform: translateY(-0.018em) rotate(0deg);
  }

  20%, 60% {
      filter: blur(0.03em);
      transform: translate(-0.018em, 0.018em) rotate(0deg);
  }

  45%, 85% {
      filter: blur(0.03em);
      transform: translate(0.018em, -0.018em) rotate(0deg);
  }

  100% {
      filter: blur(0.007em);
      transform: translate(0) rotate(-0.5deg);
  }
}

@keyframes crack1 {
  0%, 95% {
      transform: translate(-50%, -50%);
  }

  100% {
      transform: translate(-51%, -48%);
  }
}

@keyframes crack2 {
  0%, 95% {
      transform: translate(-50%, -50%);
  }

  100% {
      transform: translate(-49%, -53%);
  }
}

@media screen and (min-width:740px) {
  input{
    min-width: 34%;
  }
  .addButton{
    min-width: 10%;
  }
  .filter{
    min-width: 5%;
  }
}



